#share-bar label {
  font-family: "FranklinGothic-ExtraCond", serif;
  font-size: 25px;
  text-transform: uppercase;
  line-height: 34px;
  float: left;
  display: block; }

#share-bar a {
  display: block;
  float: left;
  width: 26px;
  height: 26px;
  background: url(../../../assets/images/sprite_main.png) no-repeat 0 0;
  text-indent: -999em;
  margin-left: 10px; }

#share-bar a.fb {
  background-position: -15px -14px; }

#share-bar a.tw {
  background-position: -58px -14px; }

#share-bar a.yt {
  background-position: -100px -14px; }

#share-bar a.fd {
  background-position: -142px -14px; }



